	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"German Media Morality","date":"13 Dec 2004","slug":"/2004-12-13-german-media-morality/","tags":"Y2004"},"html":"<p>I've been following Jeff Jarvis and his <a title=\"BuzzMachine: FCC -- and media -- duped by Brent Bozell's complaint factory\" href=\"http://www.buzzmachine.com/archives/2004_12_06.html#008600\">campaign</a> against the FCC and so-called morality in the US media. We don't have an FCC in Germany, but we do have the <em>Bild Zeitung</em> to watch over morals in the media and society... and we have <a href=\"http://www.bildblog.de/\">BildBlog</a> to watch over the <em>Bild Zeiting</em>. Lately, <em>Bild</em> has compained that <a title=\"BILDblog - Bild sprach zuerst mit dem Friseur\" href=\"http://www.bildblog.de/index.php?p=332\">a gay politician kissed a woman in public</a> (\"Is he no longer gay?\"), that <a title=\"BILDblog - Routine\" href=\"http://www.bildblog.de/index.php?p=378\">a TV nude scene was cut</a> from a mini-series (\"Is she not pretty enough for TV?\"), and that Germany's most respected <a title=\"BILDblog - Christiansen und der Hundesalon\" href=\"http://www.bildblog.de/index.php?p=377\">newscaster opened a dog salon</a> (\"Massages for rich Fifis\"). So our morals in Germany are safe, thanks to <em>Bild</em>. (And thanks to BildBlog, we can know what's in the <em>Bild Zeitung</em> without actually have to read it ourselves.)</p>"},"next":{"fields":{"title":"Expatriate Hipsters","date":"13 Dec 2004","slug":"/2004-12-13-expatriate-hipsters/"}},"previous":{"fields":{"title":"Was Kerry poisoned?","date":"13 Dec 2004","slug":"/2004-12-13-was-kerry-poisoned/"}}},{"node":{"fields":{"title":"Expatriate Hipsters","date":"13 Dec 2004","slug":"/2004-12-13-expatriate-hipsters/","tags":"Y2004"},"html":"<p><a title=\"U.S. Expatriate Hipsters Plug Into the Buzz of Berlin\" href=\"http://www.latimes.com/news/nationworld/world/la-fg-expats12dec12,0,60127.story?coll=la-home-world\">LA Times: U.S. Expatriate Hipsters Plug Into the Buzz of Berlin</a> \"Berlin is like the antidote to New York. It's all the things you want, the culture, the music scene, but none of the stress... (We) see it as a place of lower rents and better politics.\" <a title=\"C H I L L M O S T . C O M - Notes from Germany\" href=\"http://www.chillmost.com/blogarchive/2004_12_12_archive.html#110289329334486180\">(via chillmost.com)</a></p>"},"next":{"fields":{"title":"Canada and the USA PATRIOT Act","date":"13 Dec 2004","slug":"/2004-12-13-canada-and-the-usa-patriot-act/"}},"previous":{"fields":{"title":"German Media Morality","date":"13 Dec 2004","slug":"/2004-12-13-german-media-morality/"}}},{"node":{"fields":{"title":"Canada and the USA PATRIOT Act","date":"13 Dec 2004","slug":"/2004-12-13-canada-and-the-usa-patriot-act/","tags":"Y2004"},"html":"<p><a title=\"Schneier on Security: Canada and the USA PATRIOT Act\" href=\"http://www.schneier.com/blog/archives/2004/12/canada_and_the.html\">Schneier on Security: Canada and the USA PATRIOT Act</a> \"It's one thing for countries to complain about U.S. counterterrorism policies, but it's quite another for countries to reduce their commerce with the U.S. The latter will get noticed in Washington far quicker than the former.\"</p>"},"next":{"fields":{"title":"MDAC Insanity","date":"12 Dec 2004","slug":"/2004-12-12-mdac-insanity/"}},"previous":{"fields":{"title":"Expatriate Hipsters","date":"13 Dec 2004","slug":"/2004-12-13-expatriate-hipsters/"}}}],"pathPrefix":"","first":false,"last":false,"index":366,"pageCount":1101}}

